
.addr{line-height:0.44rem;height:0.44rem;font-size:0.18rem;text-align:center;width:100%;position:fixed;top:0;left:0;background:#fff;z-index:9999
}
.addr .addressBox{font-size:0.14rem;line-height:0.44rem;position:absolute;left:0;top:0;padding:0 0.14rem
}
.addr .addressBox .address{display:inline-block;vertical-align:middle
}
.addr .addressBox img{width:0.2rem;height:0.2rem;display:inline-block;vertical-align:middle;position:relative;top:-0.03rem
}
.addr .user{width:0.44rem;height:0.44rem;position:absolute;top:0;right:0;background:url("../assets/images/user.png") no-repeat center;background-size:0.18rem
}



